
.wp-block-create-block-team-member-slider-block {
	display: flex;
	flex-wrap: wrap;
	@for $i from 1 through 6{
		&.has-#{$i}-columns{
			.wp-block-create-block-team-member{
				width: calc(100%/ #{$i}) !important;
			}
		}
	}
	.wp-block-create-block-team-member{
		
		padding: 10px;
		
		img{
			margin-bottom: 20px;
		}
		h4{
			color: #222;
			font-size: 22px;
			font-weight: bold;
			margin: 0;
		}
		p{
			color:#565656;
			font-size: 16px;
			font-weight: 300;
			margin-bottom: 5px;
			margin-top: 5px
		}
	} 
}
